.contact-links{
    background:#000
}

.contact-links .fa,
.contact-links .fas,
.contact-links .fab {
    margin-right: .75rem;
}

.contact-links li {
     margin-bottom: .25rem;
     margin-top: .25rem;
    background: black;
    font-size: .85rem;
    list-style-type: none;
    padding-bottom: .5rem;
    padding-left: .5rem;
  }
  
  .contact-links a, .contact-links span  {
    color: #39ff14;
    text-decoration: none;
    position: relative;
    padding: 2px 2px;
    transition: all 0.2s ease-out;
  }

  .contact-links a:after {
    content: " ";
    width: 100%;
    height: 1px;
    background: #39ff14;
    position: absolute;
    right: 0;
    bottom: 0;
    transition: all 0.2s ease-out;
  }

  .contact-links a:before {
    content: " ";
    width: 4px;
    height: 4px;
    background: #39ff14;
    position: absolute;
    right: -4px;
    bottom: 0;
    opacity: 0;
  }

  .contact-links a:hover {
    background: rgba(57, 255, 20, 0.1);
    transition: all 0.1s ease-out;
    padding: 2px 8px;
  }

  .contact-links a:hover:after {
    width: 0px;
    transition: all 0.1s ease-out;
  }

  .contact-links a:hover:before {
    opacity: 1;
    animation: blink 0.4s infinite 0.1s;
    transition: all 0.1s ease-out;
  }
  
  @keyframes blink {
    0% {
      opacity: 1;
    }

    50% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }